.card {
    height: auto;
    margin: 0 auto;
    &__wrap {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        position: relative;
        &__slider {
            overflow: auto;
            margin: 0 auto;
            text-align: center;
            transition: all .4s ease-in-out;
            display: flex;

            &__wrapBtn {
                display: flex;
                align-items: center;
                justify-content: center; }

            &__btn {
                display: block;
                background: transparent;
                border: 0;
                outline: 0;
                cursor: pointer;

                &__left {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    top: 20px;
                    left: 250px; }
                &__right {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    top: 20px;
                    right: 250px; }

